import ReactMarkdown from "react-markdown";

const markdown = `
### Zero Trust Acceptance Criteria (ZTAC):

> *Source: Pre-decisional*

1. You are who you say you are - continuous authentication and credentialing

1. You are affirmed as an actor that is allowed to assume a role - authorization, verification and validation

1. The role you assume is fully specified - attribution and behavioral rules

1. The resources are fully ascribed with operating characterizations - attribution

1. The objects/assets (data, etc.) are ascribed with access and use characterizations - attributions - when, where and how information is allowed for use

1. Policy is sufficiently specified to dictate prescriptive actions - rules

1. The orchestration of IBAC/RBAC/ABAC and rules is computationally consistent, coherent, complete, congruent, and validated

1. Enforcement Services can apply ZT computational determinations instantly and accurately

1. Behavioral analytics track and evaluate common and uncommon patterns - ML/AI

1. Unanticipated conditions are immediately escalated for accommodation
`


const IDAFAcceptanceCriteria = () => {
    return(
        <ReactMarkdown children={markdown}/>
    )
}

export default IDAFAcceptanceCriteria;